<template>
  <div class="card">
    <div class="card-header">Exam03ReactiveData</div>
    <div class="card-body">
      <p>번호:{{ product.no }}</p>
      <p>이름: {{ product.name }}</p>
      <p>회사: {{ product.company }}</p>
      <p>가격: {{ getPrice() }}</p>
      <p>판매: {{ product.detail.sale ? "판매" : "품절" }}</p>
      <hr />
      <p class="m-2" v-html="product.detail.info"></p>
      <hr />
      <p>
        <img v-bind:src="require(`@/assets/products/${product.image}`)" class="mr-2" height="150"/>
        <span v-if="!product.detail.sale">v-if: <img width="100" src="@/assets/products/soldout.jpg"/></span>
        <span v-show="!product.detail.sale">v-show: <img width="100" src="@/assets/products/soldout.jpg"/></span>
      </p>
      <hr />
      <button v-on:click="changeData" class="btn btn-info btn-sm mr-2">변경</button>
    </div>
  </div>
</template>

<script setup>
import { reactive } from "vue";

const product = reactive({
  no: 1,
  name: "검정 미니백",
  company: "클레인",
  price: 300000,
  image: "minibag-black.png",
  detail: {
    info: "<div>시그너츠 Cecyle Lock 마그네틱 클로저가 특징입니다.</div>",
    sale: false
  }
});

function getPrice() {
  return product.price;
}

function changeData() {
  product.no++;
  product.name = "빨간 미니백";
  product.price += 1000;
  product.image = "minibag-red.png";

  product.detail.info = "<div>시그너츠 Cecyle Lock 마그네틱 클로저와 빨간 색의 조화가 좋습니다.</div>";
  product.detail.sale = !product.detail.sale;
}
</script>

