<template>
  <div class="card">
    <div class="card-header">Exam02RefData</div>
    <div class="card-body">
      <p>번호:{{ no }}</p>
      <p>이름: {{ name }}</p>
      <p>회사: {{ company }}</p>
      <p>가격: {{ getPrice() }}</p>
      <p>판매: {{ detail.sale ? "판매" : "품절" }}</p>
      <hr />
      <p class="m-2" v-html="detail.info"></p>
      <hr />
      <p>
        <img v-bind:src="require(`@/assets/products/${image}`)" class="mr-2" height="150"/>
        <span v-if="!detail.sale">v-if: <img width="100" src="@/assets/products/soldout.jpg"/></span>
        <span v-show="!detail.sale">v-show: <img width="100" src="@/assets/products/soldout.jpg"/></span>
      </p>
      <hr />
      <button v-on:click="changeData" class="btn btn-info btn-sm mr-2">변경</button>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";

let no = ref(1);
let name = ref("검정 미니백");
let company = ref("클레인");
let price = ref(300000);
let image = ref("minibag-black.png");
let detail = ref({
    info: `
        <div>
        시그너츠 Cecyle Lock 마그네틱 클로저가 특징입니다.
        </div>
    `,
    sale: false
});

function getPrice() {
  return price.value;
}

function changeData() {
  no.value++;
  name.value = "빨간 미니백";
  price.value += 1000;
  image.value = "minibag-red.png";

  /*detail.value.info = "<div>시그너츠 Cecyle Lock 마그네틱 클로저와 빨간 색의 조화가 좋습니다.</div>";
  detail.value.sale = !detail.value.sale;*/
  
  detail.value = {
    info: "<div>시그너츠 Cecyle Lock 마그네틱 클로저와 빨간 색의 조화가 좋습니다.</div>",
    sale: !detail.value.sale
  };
}
</script>

<style scoped></style>
