<template>
  <div class="card">
    <div class="card-header">
      Exam04AttrBinding
    </div>
    <div class="card-body">
      <div>
        <a :href="vueHome" class="btn btn-info btn-sm mr-2">Vue Home</a>
        <router-link :to="nextView" class="btn btn-info btn-sm mr-2">Home</router-link>
      </div>
      <hr/>
      <div><img :src="require(`@/assets/photos/${imgName}`)" height="150"/></div>
      <hr/>
      <div>
        <span class="mr-3 font-weight-bold" :class="className">아름다운 풍경</span>
        <span class="mr-3" :class="['font-weight-bold', className]">아름다운 풍경</span>
        <span class="mr-3" :class="{'font-weight-bold':isFontWeightBold, class1:isClass1, class2:isClass2}">아름다운 풍경</span>
      </div>
      <hr/>
      <div>
      <span class="mr-3" style="font-weight:bold;" :style="style">아름다운 풍경</span>
      <span class="mr-3" :style="['font-weight:bold', style]">아름다운 풍경</span>
      <span class="mr-3" style="margin-right:12px;" :style="{'font-weight':'bold', 'color':color}">아름다운 풍경</span>
      </div>
      <hr/>
      <button v-on:click="changeData" class="btn btn-info btn-sm">변경</button>
      <hr/>
      <FallthroughAttributes  id="FallthroughAttributes" 
                              class="font-weight-bold" 
                              style="color:orange" 
                              to="/" @click="handleBtn"/>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";

const vueHome = ref("https://vuejs.org");
const nextView = ref("/");
const imgName = ref("photo1.jpg");

const className = ref("class1");
const isFontWeightBold = ref(true);
const isClass1 = ref(true);
const isClass2 = ref(false);

const style = ref("color: green;");
const color = ref("green");

const toggle = ref(true);

function changeData() {
  if(toggle.value) {
    imgName.value = "photo2.jpg";
    className.value = "class2";
    isClass1.value = false;
    isClass2.value = true;
    style.value = "color: red;";
    color.value = "red";
  } else {
    imgName.value = "photo1.jpg";
    className.value = "class1";
    isClass1.value = true;
    isClass2.value = false;
    style.value = "color: green;";
    color.value = "green";
  }
  toggle.value = !toggle.value;
}

import FallthroughAttributes from "./FallthroughAttributes.vue";

function handleBtn() {
  console.log("handleBtn()");
}
</script>

<style scoped>
  .class1 { color: green; }
  .class2 { color: red; }
</style>