<template>
  <!-- 
    Fallthrough Attributes가 자동으로 DOM에 추가 및 병합됨 
    v-on(@) 이벤트 리스너일 경우 상속되므로 DOM 상에는 나타나지는 않음
  -->
  <div class="card">
    <div class="card-header">
      <span :class="$attrs.class" :style="$attrs.style">Fallthrough Attributes</span>
    </div>
    <div class="card-body">
      <router-link :to="$attrs.to" class="ml-2 btn btn-info btn-sm">이동</router-link>
      <button @click="$attrs.onClick" class="ml-2 btn btn-info btn-sm">버튼</button>
      <hr />
      <button @click="handleBtn1" class="ml-2 btn btn-info btn-sm">이동</button>
      <button @click="handleBtn2" class="ml-2 btn btn-info btn-sm">버튼</button>
    </div>
  </div>
</template>

<!-- 주석으로 할 경우, 버튼을 누르면 button 핸들러에서 한번, 루트 div에서 한번 처리됨 -->
<script>
export default {
  inheritAttrs: false,
};
</script>

<script setup>
import { useAttrs, toRaw } from "vue";
const attrs = useAttrs();

import { useRouter } from "vue-router";
const router = useRouter();

function handleBtn1() {
  router.push(attrs.to);
}

function handleBtn2() {
  attrs.onClick();
}
</script>

<style scoped></style>
